export function Header({ children }: { children?: React.ReactNode }) {
  return (
    <header
      className="fixed left-0 right-0 top-0 z-50"
      style={{
        background: 'var(--background-overlay-lightSecondary, rgba(255, 255, 255, 0.72))',
        backdropFilter: 'blur(27px)',
      }}
    >
      <div
        className="relative mx-auto flex max-w-full items-center justify-between"
        style={{
          width: '100%',
          maxWidth: '1437px',
          height: '52px',
          paddingLeft: 24,
          paddingRight: 24,
        }}
      >
        {/* Left spacer for balance */}
        <div className="flex-1 shrink-0" aria-hidden />

        {/* Center: Logo + Beta chip */}
        <div className="flex shrink-0 items-center gap-2">
          <GelatoLogoSvg />
          <span
            className="flex flex-col justify-center gap-[5.765px] self-stretch font-medium"
            style={{
              padding: '4.31px 3.47px 4.31px 4.46px',
              borderRadius: 'var(--2, 4px)',
              background: 'var(--kitkat-purple-100, #EAD9FF)',
              fontSize: '10px',
              lineHeight: 1.2,
              color: 'var(--content-primary)',
            }}
          >
          Beta
          </span>
        </div>

        {/* Right: Bookmark + Profile */}
        <div className="flex flex-1 shrink-0 items-center justify-end gap-1">
          <button
            type="button"
            className="flex shrink-0 items-center justify-center transition-opacity hover:opacity-70"
            style={{
              width: 'var(--48, 48px)',
              height: 'var(--48, 48px)',
            }}
            aria-label="찜하기"
          >
            <Image src={getImagePath("/bookmark.svg")} alt="" width={24} height={24} />
          </button>
          <button
            type="button"
            className="flex shrink-0 items-center justify-center overflow-hidden rounded-full bg-neutral-200 transition-opacity hover:opacity-70"
            style={{
              width: '40px',
              height: '40px',
              aspectRatio: '1/1',
              gap: '10px',
            }}
            aria-label="프로필"
          >
            <Image src={getImagePath("/profile.svg")} alt="" width={40} height={40} />
          </button>
        </div>

        {/* Optional overlay (e.g. search bar when on products page) */}
        {children}
      </div>
    </header>
  )
}
